
import { defineComponent, onMounted, ref } from "vue";
export default defineComponent({
  props: {
    modelValue: {
      type: [String, Number],
    },
    label: {
      type: String,
      required: true,
    },
    autoFocus: {
      type: Boolean,
      required: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: "text",
    },
  },
  setup(props, { emit }) {
    const inputReference = ref<HTMLInputElement>();

    onMounted(() => {
      const input = inputReference.value;
      if (props.autoFocus && input) input.focus();
    });

    return {
      inputReference,
      handleInput(e: Event) {
        let value: number | string = (e.target as HTMLInputElement).value;
        if (props.type === "number") value = parseInt(value);
        emit("update:modelValue", value);
      },
    };
  },
});
